import { Component, OnInit, OnDestroy } from '@angular/core';
import { GetItemsService } from '../get-items.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-app-comments',
  templateUrl: './app-comments.component.html',
  styleUrls: ['./app-comments.component.css']
})
export class AppCommentsComponent implements OnInit, OnDestroy {

  public comments = [];
  private subscription: Subscription;
  public filterName: string;
  public items =  [];

  constructor(private getItems: GetItemsService) {
   }


  addComment(comment) {
      if (comment) {
        this.getItems.setComment(comment);
      }
      this.filterName = '';
  }

  ngOnInit() {
    if (localStorage.getItem('items') === null) {
            this.comments = [];
        } else {
            this.subscription = this.getItems.getItem().subscribe(item => {
            if (item.isActive === true) {
                this.comments = item.comments;
            } else {
                this.comments = [];
            }
        });
        }
      }
    ngOnDestroy() {
        this.subscription.unsubscribe();
    }
}

