import { Component, OnInit, OnDestroy} from '@angular/core';
import { GetItemsService } from '../get-items.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-app-items',
  templateUrl: './app-items.component.html',
  styleUrls: ['./app-items.component.css']
})
export class AppItemsComponent implements OnInit, OnDestroy {

  public filterName: string;
  public items = [];
  public isActive: boolean;
  private subscription: Subscription;
  public selectedItem: string;

  constructor(private getItems: GetItemsService) {
      this.subscription = this.getItems.getComment().subscribe(data => {
            this.items.forEach(item => {
                if (item.isActive === true) {
                    item.comments.push(data);
                }
            });
            localStorage.setItem('items', JSON.stringify(this.items));
      });
   }

  addNewItem(name) {
    if (name) {
        this.items.push({
            'name': name,
            'comments': [],
        });
        localStorage.setItem('items', JSON.stringify(this.items));
      this.filterName = '';
    }
  }

  deleteItem(item) {
    const delSensor = this.items.indexOf(item);
    this.items.splice(delSensor, 1);
    localStorage.setItem('items', JSON.stringify(this.items));
  }

  selectItem(item) {
        item.isActive = !item.isActive;
        this.getItems.setItem(item);
        this.selectedItem = item;
        localStorage.setItem('items', JSON.stringify(this.items));
  }

  ngOnInit() {
    if (localStorage.getItem('items') === null) {
            this.items = [];
        } else {
        const items = localStorage.getItem('items');
        const parsedItems = JSON.parse(items);
        this.items = parsedItems;
    }
    this.items.forEach(item => {
        item.isActive = false;
        localStorage.setItem('items', JSON.stringify(this.items));
    });

}
    ngOnDestroy() {

    }
}
